import Vue from "vue";
import App from "./App";
import "mint-ui/lib/style.css";
// import './mockjs/mock'

import Mint from "mint-ui";
Vue.use(Mint);

import Axios from "axios";
Vue.prototype.$axios = Axios;

Vue.config.devtools = true;

new Vue({
  el: "#app",
  components: { App },
  template: "<App/>",
  watch: {}
});

// classList 安卓3.0开始支持 ; getAttribute chrome 1支持
window.addEventListener("click", function(e) {
  // debugger;
  let dom = e.target;
  let hoverclass = dom.getAttribute && dom.getAttribute("dentHoverclass");
  //可能点击的是子元素 往上找一层
  if (!hoverclass) {
    dom = dom.parentNode;
    dom.getAttribute && (hoverclass = dom.getAttribute("dentHoverclass"));
  }
  if (hoverclass) {
    clearTimeout(dom.timer);
    dom.classList.add(hoverclass);
    dom.timer = setTimeout(() => {
      dom.classList.remove(hoverclass);
      clearTimeout(dom.timer);
    }, 500);
  }
});
//时间格式化
// https://www.cnblogs.com/sexintercourse/p/6490921.html author: meizz
Date.prototype.Format = function(fmt) {
  var o = {
    "M+": this.getMonth() + 1, //月份
    "d+": this.getDate(), //日
    "h+": this.getHours(), //小时
    "m+": this.getMinutes(), //分
    "s+": this.getSeconds(), //秒
    "q+": Math.floor((this.getMonth() + 3) / 3), //季度
    S: this.getMilliseconds() //毫秒
  };
  if (/(y+)/.test(fmt))
    fmt = fmt.replace(
      RegExp.$1,
      (this.getFullYear() + "").substr(4 - RegExp.$1.length)
    );
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length)
      );
  return fmt;
};

